<template>
    <form name="heroAddForm"  autocomplete="off">
        <table class="table table-edit">
            <tr>
                <td width="90px"></td>
                <td width="180px">
                	  <select name="" v-model="heroInfo.hero_type" class="form-control input-sm">
                	  	   <option value="">--  --</option>
                	  	   <option value="1"></option>
                	  	   <option value="2"></option>
                	  	   <option value="3"></option>
                	  </select>
                </td>
                <td  width="80px"></td>
                <td  width="180px"><input type="number" name="" v-model="heroInfo.order" class="form-control input-sm"></td>
            </tr>
            <tr>
            	<td></td>
            	<td colspan="3">
            		<textarea class="form-control input-sm" v-model="heroInfo.hero_cn_name" rows="4" placeholder=""></textarea>
            	</td>
            </tr>
            <tr>
            	<td></td>
            	<td colspan="3">
            		<textarea class="form-control input-sm" v-model="heroInfo.hero_en_name" rows="4" placeholder=""></textarea>
            	</td>
            </tr>
            <tr>
                <td></td>
                <td colspan="3">
                	<textarea class="form-control input-sm" v-model="heroInfo.hero_img" rows="4" placeholder=""></textarea>
                </td>              
            </tr>
        </table>
    </form>
</template>

<script>
export default {
	  name:"heroAdd",
	  props:["heroInfo"]
}
</script>








