import Vue from 'vue'
import Vuex from 'vuex'

/* vue  vuex*/
Vue.use(Vuex)

/*state*/
const state = {
   token: "",
   user: {}
}


/*Getters store  state */
const getters = {
  getLoginToken: state => { return state.token },
  getLoginUser: state => { return state.user },
}

/*
mutations mutation;
 Vuex  store  store.commit mutation,store.commit('setAuth', true);
*/
const mutations = {
  // TODO: 
  setLoginToken (state, token) {
     console.log("setLoginToken========"+token);
     localStorage.setItem("token",token)
     state.token = token;

     //console.log(Vue.$axios);
     //this.$axios.defaults.headers.common['Authorization'] = `Bearer ${accessToken}`
  },
  setLoginUser (state, user) {
     console.log("setLoginUser========"+user);
     localStorage.setItem("user",JSON.stringify(user))
     state.user = user;
  }

}

//  store
//  store 
export default new Vuex.Store({
  state,
  getters,
  mutations,
})



